/* Skills.css */

.skills-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 20px 50px 20px;
}

.skill-card p{
  color: #333;
  font-size: 1.2rem;
  font-weight: 600;
}

.skill-card p:hover{
  color: #ed29c6;
  
}

.skills-category {
  background-color: #050137;
  padding: 30px;
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  border-radius: 20PX;
  justify-content: space-around;
  align-items: center;
}

.skill-card {
  background-color: #f1f1f1;
  padding: 15px;
  width: 120px;
  height: 120px;
  text-align: center;
  border-radius: 8px;
  margin-bottom: 15px;
  transition: transform 0.3s ease-in-out;
}

.skill-card:hover {
  transform: scale(1.05);
  filter: grayscale(100%);
}

.skill-card img {
  width: 60px;
  height: 60px;
  
}

@media screen and (max-width: 576px) {
  
  .skill-card {
    background-color: #f1f1f1;
    padding: 10px;
    width: 120px;
    height: 120px;
    text-align: center;
    border-radius: 8px;   
    transition: transform 0.3s ease;
  }
  .skill-card img {
    width: 60px;
    height: 60px;   
  }

  .skill-card img:hover{
    filter: grayscale(100%);
  }

}
