/* Educatio.css */

.education {
    background: #efefef;    
    padding: 40px;    
       
  }   
  
  .education .box-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .education .box-container .box {
    display: flex;
    flex-direction: row;
    width: 70%;
    border-radius: 0.5rem;
    box-shadow: 0.2rem 0.5rem 1rem rgba(0, 0, 0, 0.2);
    text-align: center;
    position: relative;
    margin-top: 1.5rem;
    overflow: hidden;
    transition: 0.3s;
    background: linear-gradient(to bottom right, #ffffff, #e5ecfb); /* Adjust the gradient colors */
  }
  
  .education .box-container .box:hover {
    transform: scale(1.03);
    box-shadow: 1rem 0.5rem 1.2rem rgba(0, 0, 0, 0.3);
  }
  
  .education .box-container .box .image {
    flex: 1 1 20rem;
    width: 100%;
  }
  
  .education .box-container .box img {
    object-fit: cover;
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 0.5rem 0 0 0.5rem; /* Adjust border-radius for image */
  }
  
  .education .box-container .box .content {
    position: relative;    
    flex-direction: column;    
    flex-wrap: wrap;
    flex: 1 1 70rem;
  }
  
  .education .box-container .box .content h3 {
    font-size: 1.5rem;
    color: #012970;    
    font-weight: 700;
    text-align: left;
    margin-left: 1rem;
  }
  
  .education .box-container .box .content p {
    font-size: 1.2rem;
    margin-left: 1rem;
    text-align: left;
  }
  
  .education h4 {
    font-size: 1rem;
    color: #226d00;
    text-align: left;
    margin: 1rem;
    font-family: "Nunito", sans-serif;
    
  }
  
  /* education media queries starts */
  @media screen and (max-width: 600px) {
    .education .box-container .box {
      flex-direction: column;
      width: 100%;
    }
  
    .education .box-container .box .image {
      width: 100%;
      height: 5rem;
    }
  
    .education .box-container .box img {
      width: 100%;
      border-radius: 0.5rem 0.5rem 0 0; /* Adjust border-radius for image */
    }
  
    .education .box-container .box .content {
      position: relative;      
      flex-direction: column;
      padding: 0.4rem;
      flex-wrap: wrap;
      flex: 0;
    }
  }
 
  