/* Hero.css */

.hero-container {
    background-size: cover;
    background-position: center;
    height: 100vh;
    display: flex;
    flex-direction: column; /* Stack children vertically */
    justify-content: center; /* Center vertically */
    align-items: center; /* Center horizontally */
    overflow: hidden; /* Ensure the animation doesn't overflow */
  }

  
  @keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  .hero-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, #3498db, #e74c3c, #9b59b6, #3498db, #03d100 );
    background-size: 400% 400%;
    animation: gradientAnimation 6s infinite;
  }

  
.hero-content {
    text-align: center;
    color: #ffffff; /* Adjust text color for better visibility on your background image */
    position: relative; /* Ensure the z-index works properly */
    z-index: 1;     
  }
/* 
  .hero-content h1 span {
    color: #bc4f00;
    font-weight: 800;   
  } */

.hero-content p {
    font-size: 1.6em;
    color: #ffffff;
    font-weight: 600;
    padding: 1rem 0;
  }
  
  .hero-content .typing-text {
    color: #222;
    font-weight: 800;
    text-shadow: -1px 1px 0 #fff,
    1px 1px 0 #ffffff,
    1px -1px 0 #fff,
    -1px -1px 0 #fff;
  }

  .hero-content .content p span {
    font-size: 1.5rem;
    color: rgb(148, 8, 8);
    font-weight: 600;
    padding: 1rem 0;
  }
 
  .profile-image {
    width: 100%;
    max-width: 200px;
    border-radius: 50%;
    transform: translateZ(50px);
    transition: transform 0.5s ease-in-out; /* Add transition for the scale effect */
  }
  
  h1 {
  font-family: 'Arial', sans-serif;
  margin: 0;
  font-size: 3em;
  padding: 0;
  margin-bottom: 10px;
  color: #333; /* Adjust the text color as needed */
  transform: translateZ(50px); /* Adjust the translation along the Z-axis as needed */
  transition: transform 0.5s ease-in-out;
  
}

  p {
    font-size: 1.5em;
    margin-bottom: 20px;
  }

  .typing-text {
    display: inline-block;
    background: rgba(255, 255, 255, 0.8);
    padding: 0 5px;
    border-radius: 3px;
  }
  
.cta-button {
    display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #3498db;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transform: translateZ(20px);
  transition: transform 0.5s ease-in-out;
  }
  
  .cta-button:hover {
    background-color: #2980b9;
    transform: scale(1.05); /* Add a scale effect on hover */
  }
  
  .social-icons {
    display: flex;
    justify-content: center;
    margin-top: 20px; 
 
  }  
 
.social-icons a {
    margin: 0 10px;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #ffffff;
    transition: color 0.3s ease, transform 0.3s ease; /* Add transition for color change and scale effect */
  }
  
  .social-icons a:hover {
    color: #ff7b00; /* Hover color */
    transform: scale(1.2); /* Add a scale effect on hover */
  }
  
  .social-icons img {
    width: 30px; /* Adjust the size of your social media logos */
    height: 30px;
    background-color: #fff;
    border-radius: 10%;
  }
  