/* About.css */

.about {
  background-color: #efefef;
  padding: 40px;
  text-align: center;
}

.about-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-text {
  flex-basis: 100%; /* Take full width in mobile view */
  padding: 20px;
  text-align: left;
  order: 0;
}

.about h2 {
  color: #333;
  font-size: 2em;
  margin-bottom: 20px;
}

.about span {
color: #285fb2;
}

.about-content .heading-container{ 
justify-content: left;
align-items: left;
}

.about p {
  color: #555;
  font-size: 1.1em;
  margin-bottom: 10px;
}

.about-image {
  flex-basis: 100%; /* Take full width in mobile view */
  margin-top: 6px;
   /* Add space between text and image in mobile view */
}

.about-image img {
  max-width: 50%;
  border-radius: 15%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  animation: mymove 5s infinite;  
}

.resume-button {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 10px;
  background-color: #3498db;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.resume-button:hover {
  background-color: #dadcdd;
  color: #333;
  border: 1px solid #000;
}


@keyframes mymove {
from {left: 0px;}
to {left: 200px;}
}

/* Responsive Styles */
@media only screen and (min-width: 768px) {
  .about-content {
    flex-direction: row;
  }

  .about-image {
    flex-basis: 50%;
    margin-top: 0; /* Remove margin-top for larger screens */
    order: 0; /* Change the order for larger screens */
  }

  .about-text {
    flex-basis: 50%;
    order: 1; /* Change the order for larger screens */
  }
  
}
