/* Contact.css */

.contact {
    background-color: #f9f9f9;
    padding: 40px;    
    align-items: center;
    
  }
    
  form {
    max-width: 500px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  label {
    margin: 10px;
    color: #fff;
    font-weight: bold;
    float: left;
    font-size: 1.2em;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 15px;    
    margin-bottom: 10px;
    margin-left: 20px;
    background-color: #eae4e4;
    margin-right: 20px;    
    border: 1px solid #333;
    border-radius: 5px;
    transition: border-color 0.3s ease;
  }
  
  input:focus,
  textarea:focus {
    outline: none;
    border-color: #007bff; /* Change border color on focus */
    
  }
  
  button {
    background-color: #444;
    color: #fff;
    padding: 10px 25px;
    font-size: 1em;
    cursor: pointer;
    border: 1px solid #fff;
    border-radius: 5px;
    margin-top: 20px;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #0056b3;
  }

  .contact-form{    
    padding: 40px;
    margin: 40px;     
    border-radius: 20px;     
    background:
    linear-gradient(45deg, #ff0000, #333),
    linear-gradient(135deg, #00ff00, #0000ff);
  }
  
  /* Animation */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  .fade-in {
    animation: fadeIn 1s ease;
  }
  